.products-list {
  max-width: 1280px;

  @media screen and (min-width: 768px) {
    display: flex;
    margin: -8px;
  }

  @media screen and (min-width: 1280px) {
    margin: -16px;
    margin-left: auto;
    margin-right: auto;
  }
}

.products-list__item {
  width: calc(100% - 200px);
  border-radius: 24px;
  margin-top: 148px;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 40px;

  @media screen and (max-width: 767px) {
    min-width: 280px;
  }
  @media screen and (min-width: 768px) {
    width: calc((100% - 3 * 16px) / 3);
    margin: 128px 8px 8px 8px;
  }

  @media screen and (min-width: 1280px) {
    width: calc((100% - 3 * 32px) / 3);
    margin: 213px 16px 16px 16px;
  }

  &--cream {
    background-color: $ice-cream-pl-bg-color;
  }

  &--coffee {
    background-color: $ice-coffee-pl-bg-color;
  }

  &--milk {
    background-color: $milkshakes-pl-bg-color;
  }
}

.products-list__title {
  font-family: 'Titan One';
  font-weight: 400;
  font-size: 20px;
  line-height: 1.15;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: $secondary-color;

  &--cream::before {
    display: block;
    float: left;
    margin-top: -140px;
    margin-bottom: 20px;
    content: '';
