.dots {
  margin: 32px auto;
}

.products-list__text {
  width: 200px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 32px;
  font-family: 'DM Sans';
  font-weight: 700;
  font-size: 14px;
  line-height: 1.52;
  text-align: center;
  letter-spacing: 0.04em;

  color: $secondary-color;
}

.products-list__button {
  display: flex;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  background-color: $secondary-color;
  border: none;
  transition: 250ms $timing-function;
  &:hover {
    transform: scale(1.1);
    background-color: $primary-color;
    .button-icon {
      fill: $secondary-color;
    }
  }
}

.button-icon {
  width: 40px;
  height: 40px;
  fill: $primary-color;
}
// ---------------------------------------SLIDER----------------------------------

.slick-list {
  overflow: hidden;
}

.slick-track {
  display: flex;
  align-items: flex-start;
}

.slick-dots {
  display: flex;
  justify-content: center;
  // align-items: center;
}

.slick-dots li {
  margin-left: 4px;
  line-height: 6px;
}

.slick-dots button {
  font-size: 0;
  padding: 0;
  border: 0;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: $secondary-color;
}

.slick-dots li.slick-active button {
  background-color: $primary-color;
}
