@mixin bc($class, $color) {
  .#{$class} {
    background-color: $color;
  }
}
.galerry {
  background-image: linear-gradient(
    to bottom,
    #fff5f6 0%,
    #fff5f6 40%,
    #fff 40%,
    #fff 100%
  );
  min-height: 122px;
  @media screen and (min-width: 768px) {
    min-height: 264px;
  }
  @media screen and (min-width: 1280px) {
    min-height: 430px;
  }
}
.container--gallery {
  width: 320px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  @media screen and (min-width: 500px) {
    width: 500px;
  }
  @media screen and (min-width: 768px) {
    width: 768px;
  }
  @media screen and (min-width: 1280px) {
    width: 1280px;
  }
}
.gallery__box {
  &:not(:first-child) {
    margin-left: 2px;
    @media screen and (min-width: 1280px) {
      margin-left: 5px;
    }
  }
}
.gradient {
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgba(255, 192, 203, 0.5);
  width: 140px;
  height: 140px;
}
.gallery__img-box {
  position: relative;
  overflow: hidden;
  border-radius: 10px;
  width: 38px;
  height: 38px;
  cursor: pointer;
  @media screen and (min-width: 500px) {
    width: 64px;
    height: 64px;
    border-radius: 16px;
  }
  @media screen and (min-width: 768px) {
    width: 87px;
    height: 87px;
    border-radius: 20px;
  }
  @media screen and (min-width: 1280px) {
    width: 140px;
    height: 140px;
    border-radius: 35px;
  }
  &:hover .galerry__see {
    opacity: 1;
  }
  &:hover .gallery__img {
    position: absolute;
    transform: scale(1.3) rotate(5deg);
  }
}

.gallery__img {
  transition: 250ms $timing-function;
}
.galerry__see {
  padding: 0;
  margin: 0;
  font-size: 30px;
  font-weight: bold;
  color: #fff;
  background-color: rgba(199, 19, 62, 0.66);
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  transition: opacity 500ms ease;
}

// @include bc(one, rgb(0, 255, 255));
// @include bc(two, rgb(0, 0, 255));

.three {
  margin-top: 2px;
  @media screen and (min-width: 1280px) {
    margin-top: 5px;
  }
}
.four {
  width: 78px;
  height: 78px;
  margin-top: 0;
  @media screen and (min-width: 500px) {
    width: 128px;
    height: 128px;
  }
  @media screen and (min-width: 768px) {
    width: 174px;
    height: 174px;
  }
  @media screen and (min-width: 1280px) {
    width: 280px;
    height: 280px;
  }
}
.five {
  margin-top: 2px;
  @media screen and (min-width: 1280px) {
    margin-top: 5px;
  }
}
// @include bc(six, rgb(210, 105, 30));

.seven {
  margin-top: 2px;
  @media screen and (min-width: 1280px) {
    margin-top: 5px;
  }
}
// @include bc(eight, rgb(0, 0, 0));

.nine {
  margin-top: 2px;
  @media screen and (min-width: 1280px) {
    margin-top: 5px;
  }
}
// @include bc(ten, rgb(127, 255, 0));

.tr-one {
  transform: translateY(5px);
  @media screen and (min-width: 768px) {
    transform: translateY(15px);
  }
  @media screen and (min-width: 1280px) {
    transform: translateY(20px);
  }
}
.tr-two {
  transform: translateY(-5px);
  @media screen and (min-width: 768px) {
    transform: translateY(-15px);
  }
  @media screen and (min-width: 768px) {
    transform: translateY(-20px);
  }
}
.tr-three {
  transform: translateY(3px);
  @media screen and (min-width: 768px) {
    transform: translateY(8px);
  }
  @media screen and (min-width: 768px) {
    transform: translateY(12px);
  }
}
.gallery__slide {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 192, 203, 0.5);
  transition-property: transform, background-color, opacity, visibility;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.gallery__bd-box {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 90%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  @media screen and (min-width: 768px) {
    width: 80%;
  }
  @media screen and (min-width: 1280px) {
    width: 70%;
  }
}
.gallery__window {
  position: relative;
  display: block;
  overflow: hidden;
  border-radius: 10%;
  width: 180px;
  height: 180px;
  @media screen and (min-width: 550px) {
    width: 350px;
    height: 350px;
  }
  @media screen and (min-width: 768px) {
    width: 400px;
    height: 400px;
  }
  @media screen and (min-width: 1280px) {
    width: 500px;
    height: 500px;
  }
}
.gallery__bd-img {
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  transition: opacity 500ms ease;
  width: 180px;
  height: 180px;
  @media screen and (min-width: 550px) {
    width: 350px;
    height: 350px;
  }
  @media screen and (min-width: 768px) {
    width: 400px;
    height: 400px;
  }
  @media screen and (min-width: 1280px) {
    width: 500px;
    height: 500px;
  }
}
.gallery__close {
  position: absolute;
  right: 2%;
  top: 2%;
  width: 25px;
  height: 25px;
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  border-radius: 50%;
  transition: 250ms $timing-function;
  &:hover {
    background-color: #c7133e;
    border-radius: 50%;
  }
  &:hover .gallery__btn-close {
    transform: scale(0.6);
  }
  @media screen and (min-width: 768px) {
    width: 45px;
    height: 45px;
  }
  @media screen and (min-width: 1280px) {
    width: 50px;
    height: 50px;
  }
}
.gallery__btn-close {
  width: 25px;
  height: 25px;
  transform: scale(0.6);
  stroke: #fff;
  stroke-width: 2px;
  @media screen and (min-width: 768px) {
    width: 45px;
    height: 45px;
  }
  @media screen and (min-width: 1280px) {
    width: 50px;
    height: 50px;
  }
}
.gallery__back {
  position: absolute;
  left: 2%;
  top: 50%;
  transform: translateY(-50%) scale(1);
  cursor: pointer;
  rotate: 180deg;

  // @media screen and (min-width: 1000px) {
  //   &:hover {
  //     left: 0;
  //   }
  // }
  @media screen and (min-width: 1000px) {
    &:hover .gallery__btn-toggle {
      transform: scale(1.1);
    }
  }
  visibility: hidden;
  @media screen and (min-width: 550px) {
    visibility: visible;
  }
}
.gallery__next {
  position: absolute;
  right: 2%;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  // transition: right 500ms ease;
  // @media screen and (min-width: 1000px) {
  //   &:hover {
  //     right: 0;
  //   }
  // }
  @media screen and (min-width: 1000px) {
    &:hover .gallery__btn-toggle {
      transform: scale(1.1);
    }
  }
  visibility: hidden;
  @media screen and (min-width: 550px) {
    visibility: visible;
  }
}
.gallery__bd-toggle {
  width: 30px;
  height: 30px;
  background-color: transparent;
  padding: 0;
  border: none;
  @media screen and (min-width: 768px) {
    width: 45px;
    height: 45px;
  }
  @media screen and (min-width: 1280px) {
    width: 60px;
    height: 60px;
  }
}
.gallery__btn-toggle {
  width: 30px;
  height: 30px;
  transition: 250ms $timing-function;

  @media screen and (min-width: 768px) {
    width: 45px;
    height: 45px;
  }
  @media screen and (min-width: 1280px) {
    width: 60px;
    height: 60px;
  }
}
.gallery__mobile-btn {
  display: flex;
  justify-content: center;
  margin-top: 10px;
  flex-basis: 100%;
  @media screen and (min-width: 550px) {
    display: none;
  }
}
.gallery__mb-btn {
  padding: 0;
  margin: 0;
  border: none;
  background-color: transparent;
}
.gallery__mb-back {
  margin-right: 10px;
  transform: rotate(180deg);
}
.gallery__mb-next {
  margin-left: 10px;
}
.gallery__mb-btn {
  width: 30px;
  height: 30px;
}
.gallery__img-visible {
  opacity: 1;
}
.galerry__is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transform: scale(0);
}
